	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Report Card Vacation","date":"31 Jan 2007","slug":"/2007-01-31-report-card-vacation/","tags":"Y2007"},"html":"<p>This week the schoolkids in Niedersachsen get 2 days off school for *Zeugnisferien*, or report card vacation. As far as I know, the first graders at Christopher's school don't even get report cards, but they get the days off anyway. </p>\n<p>Even better, today his school lets out at 10 am, after exactly 90 minutes in class. So working parents lose 3 days instead of 2. </p>\n<p>That din you'll be hearing in the background is the sound of somewhat more chaos than usual.</p>"},"next":{"fields":{"title":"Airline Crap Shoot 2007","date":"30 Jan 2007","slug":"/2007-01-30-airline-crap-shoot-2007/"}},"previous":{"fields":{"title":"Bring Slippers to School","date":"04 Feb 2007","slug":"/2007-02-04-bring-slippers-to-school/"}}},{"node":{"fields":{"title":"Airline Crap Shoot 2007","date":"30 Jan 2007","slug":"/2007-01-30-airline-crap-shoot-2007/","tags":"Y2007"},"html":"<p>We've flown between Hamburg and Minneapolis (either starting from there or starting from here) pretty much every year for the past 25 years. We'll be doing it again this summer. And we know the choices better than any travel agent or web site. There are no direct flights, there haven't been since the '80s. If you want one stop, you either have to fly direct Hamburg to North America (currently Continental or Emirates) or Minneapolis to Europe (Northwest or Northwest). My wife collects hundreds of thousands of <a href=\"http://www.miles-and-more.com/\">Lufthansa miles</a>, but usually they don't help us much (this year the closest they could get us was Newark).</p>\n<p>You also would like to avoid an extra stop in the US if you can, since US Immigration is a risk factor. If you can't avoid it, you should allow at least 3 hours between flights. We've missed connections and had luggage delayed by cutting the connections too close. That would have limited our options to Northwest. They were OK last year, but the flights with the <a href=\"http://www.papascott.de/archives/2006/08/18/back-again/\">old DC-10s</a> were somewhat cramped.</p>\n<p>This year, though, we remembered that <a href=\"http://www.icelandair.com/\">Icelandair</a> also flies to Minneapolis. The regular travel sites wouldn't show us Icelandair flights, since there's no good way to get from Hamburg to Reykjavik. But flying from Frankfurt was 4 hours shorter than any other flight we checked (just 1 hour layover at Keflavik), the cost was several hundred Euros less than the other flights, and we have all those Lufthansa miles to get to Frankfurt.</p>\n<p>We flew Icelandair once to Florida, and we were quite impressed. We promised ourselves to fly with them again if it were practical. <a href=\"http://www.airport.is/english/menu/facilities/facility_map/\">Keflavik</a> is a small airport, so transfers are easy. And <a href=\"http://www.icelandair.com/home/travel-information/in-flight/seat-map/\">they fly 757s</a>, with rows only 6 across. We're guaranteed to sit together, with a window seat for Christopher, and noone will get stuck in the middle aisle.</p>\n<p>So Iceland here we come (for one hour). We've got 2 stops and have to fly through the Monster Maze in Frankfurt, but the shorter total time, the smaller planes, and the quick transfer in Reykjavik should make for a good flight.</p>"},"next":{"fields":{"title":"No thanks, I'll just drive my radio","date":"29 Jan 2007","slug":"/2007-01-29-no-thanks-ill-just-drive-my-radio/"}},"previous":{"fields":{"title":"Report Card Vacation","date":"31 Jan 2007","slug":"/2007-01-31-report-card-vacation/"}}},{"node":{"fields":{"title":"No thanks, I'll just drive my radio","date":"29 Jan 2007","slug":"/2007-01-29-no-thanks-ill-just-drive-my-radio/","tags":"Y2007"},"html":"<p>He: If anyone asks*, for my birthday I'd like money to get myself a new car radio.</p>\n<p>She: Your car radio works just fine. Why don't you buy a new car?</p>\n<p>He: ?!?</p>\n<p>(*Actually the question \"What do you want for Christmas/your birthday\" could be the subject of an entire expat post, although I'm not sure if all Germans are obsessive about this question, or just my in-laws. They also always ask my wife what I want instead of me personally. Maybe they got tired of me answering \"World peace\".)</p>"},"next":{"fields":{"title":"One Good Thing About Germany","date":"26 Jan 2007","slug":"/2007-01-26-one-good-thing-about-germany/"}},"previous":{"fields":{"title":"Airline Crap Shoot 2007","date":"30 Jan 2007","slug":"/2007-01-30-airline-crap-shoot-2007/"}}}],"pathPrefix":"","first":false,"last":false,"index":159,"pageCount":1101}}

